<template>
  <div>
    <div id="treeMount"></div>
  </div>
</template>
<script>
import $ from "jquery";
export default {
  name: "Tree",
  /**
   * data ： 数据源
   * extend ： 默认是否展开树，false不展开
   * hideTopNode ：是否隐藏顶级节点，true表示隐藏
   * showCheckbox ： 是否显示复选框
   * height ：ul的高度，默认为300px
   */
  props: {
    data: {
      type: Array,
      default: function () {
        return [];
      },
      required: true,
    },
    height: {
      type: Number,
      default: 300,
    },
    extend: {
      type: Boolean,
      default: false,
    },
    hideTopNode: {
      type: Boolean,
      default: false,
    },
    showCheckbox: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      // 展开和收起tree的设置，来自props的extend
      treeExtend: false,
      // 所有checkbox选中的项目
      treeData: [],
    };
  },
  methods: {
    // 初始化Tree目录
    init() {
      //  如果顶级节点隐藏，那么必须展开树，也就是说extend属性的设置失效，默认为true
      if (this.hideTopNode == true) {
        this.treeExtend = true;
      }
      // 根节点
      let ul = $("<ul id='tree'></ul>");
      // 递归生成目录树
      this.generatorTree(this.data, ul);
      // 如果需要隐藏根节点，则取出根节点下面的第一个ul作为根节点
      if (this.hideTopNode) {
        ul = $(ul.find("ul")[0]);
      }
      // 取消顶级ul的padding-left
      ul.css({
        "padding-left": "0px",
        "list-style": "none",
        "overflow-y": "auto",
        height: `${this.height}px`,
      });
      // 挂载tree目录
      $("#treeMount").empty();
      $("#treeMount").append(ul);
    },
    /**
     * 监听checkbox的变化，主要是子box都选中后，父box默认进入全选状态；
     * 否则任意一个子box的状态为未选中，父box为反选
     */
    watchCheckbox(checkbox) {
      // 处理选中样式的变化
      let li = $(checkbox).parent("li").parentsUntil("li").parent();
      let checked = true;
      for (let input of li.find("li").find("input")) {
        if ($(input).prop("checked") == false) {
          checked = false;
          break;
        }
      }
      $(li.children("input")).prop("checked", checked);
    },
    /**
     * 获取tree目录上所有选中的节点的数据
     */
    watchTreeData() {
      let inputs = $("input:checked");
      this.treeData = [];
      for (let i of inputs) {
        let id = $(i).val();
        this.findTreeData(id, this.data);
      }
    },
    /**
     * 递归遍历tree目录，搜索符合当前节点的数据
     */
    findTreeData(id, data) {
      for (let item of data) {
        if (item.id == id) {
          this.treeData.push(item);
        } else {
          this.findTreeData(id, item.children);
        }
      }
    },
    // 递归生成树
    generatorTree(data, container) {
      for (let item of data) {
        let li = $("<li></li>");
        // 增加id属性
        li.attr("id", item.id);
        // 如果有checkbox，处理checkbox的全选，反选等事件
        if (this.showCheckbox) {
          let box = $(`<input type='checkbox' value=${item.id}>`);
          box.css({
            width: "16px",
            height: "16px",
            "vertical-align": "top",
          });
          box.on("click", (event) => {
            let checkbox = event.target;
            let li = $(checkbox).parent("li");
            // 全选和反选
            if ($(checkbox).prop("checked")) {
              li.find("input").prop("checked", true);
            } else {
              li.find("input").prop("checked", false);
            }
            // 监听每一次checkbox的变化
            this.watchCheckbox(checkbox);
            this.watchTreeData();
          });
          li.append(box);
        }
        // 向外传递的事件
        // 如果有复选框，则给复选框添加单击事件，否则给li添加
        if (this.showCheckbox) {
          li.on("click", "input", (event) => {
            event.stopPropagation();
            if ($(event.target).prop("checked")) {
              this.$emit("row", item);
            } else {
              this.$emit("row", {});
            }
            this.$emit("rows", this.treeData);
          });
        } else {
          li.on("click", (event) => {
            event.stopPropagation();
            this.$emit("row", item);
          });
        }

        // extend为false表示收起所有折叠，true表示展开
        if (this.treeExtend == false) {
          li.css({ height: "22.5px", overflow: "hidden" });
        }
        // 增加标签内容
        let span = $("<span></span>");
        // 计算标签的宽度
        let spanWidth = 0;
        if (item.children && item.children.length > 0) {
          spanWidth += 23;
        }
        if (this.showCheckbox) {
          spanWidth += 23;
        }

        span.css({
          display: "inline-block",
          width: `calc(100% - ${spanWidth}px)`,
          "vertical-align": "middle",
          overflow: "hidden",
          "text-overflow": "ellipsis",
          "white-space": "nowrap",
          "text-align": "left",
        });
        span.text(item.label);
        li.append(span);
        container.append(li);

        // 判断是否有孩子
        if (item.children && item.children.length > 0) {
          // 生成上下的箭头
          let img = $("<img />");
          img.attr(
            "src",
            "data:image/png;base64,"
          );
          img.css({
            width: "15px",
            height: "15px",
            margin: "3px 3px 3px 4px",
            "vertical-align": "middle",
            // 箭头展开折叠的角度变化
            transform: this.extend ? "rotate(0deg)" : "rotate(-90deg)",
          });
          img.on("click", (event) => {
            event.stopPropagation();
            //   折叠处理
            if (li.css("height") == "22.5px") {
              li.css({ height: "auto" });
              img.css({
                transform: "rotate(0deg)",
              });
            } else {
              li.css({ height: "22.5px", overflow: "hidden" });
              img.css({
                transform: "rotate(-90deg)",
              });
            }
          });
          li.prepend(img);
          // 生成孩子列表
          let ul = $("<ul></ul>");
          ul.css({ "list-style": "none", overflow: "hidden" });
          li.append(ul);
          this.generatorTree(item.children, ul);
        }
      }
    },
  },
  watch: {
    data(val) {
      if (val && val.length > 0) {
        this.init();
      }
    },
  },
  created() {
    this.treeExtend = this.extend;
  },
  mounted() {
    this.init();
  },
};
</script>
<style lang="less" scoped>
ul,
li {
  list-style: none;
}
#treeMount {
  font-size: 16px !important;
}
/deep/::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 3px; /*高宽分别对应横竖滚动条的尺寸*/
  height: 1px;
}
/deep/::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 3px;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: #aaa;
}
/deep/::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background: #ededed;
}
</style>